<template>
    <div>
        <div class="login-forms">
            <div class="alert alert-secondary" role="alert">
                <div> <span>Email:</span>eve.holt@reqres.in </div>
                <div><span>password</span>cityslicka</div>
            </div>
            <form action="#">
                <div class="form-group">
                    <label>Email</label>
                    <input type="email" class="form-control" name="username" v-model.trim="$v.username.$model"
                        :class="{'is-invalid':$v.username.$error,'is-valid':!$v.username.$invalid}">
                    <div class="valid-feedback">your Email is vaild! </div>
                    <div class="invalid-feedback">
                        <span v-if="!$v.username.required">Your Email is requried</span>
                    </div>
                </div>
                <div class="form-group">
                    <label>Password</label>
                    <input type="password" class="form-control" name="password" v-model.trim="$v.password.$model"
                        :class="{'is-invalid':$v.password.$error, 'is-valid':!$v.password.$invalid}">
                    <div class="valid-feedback">your password is vaild! </div>
                    <div class="invalid-feedback">
                        <span v-if="!$v.password.required">Your password is requried</span>
                        <span v-if="!$v.password.minLength"> your Password is have least
                            {{$v.password.$params.minLength.min}}</span>
                    </div>
                </div>
            </form>
            <button type="submit" class="btn" @click="Login()">Sign in</button>
            <router-link to="/registration" class="custom-link">Register</router-link>
        </div>
    </div>
</template>

<script>
    import {
        required,
        minLength,
        email
    } from 'vuelidate/lib/validators';
    import axios from "axios";
    export default {
        name: 'login-forms',
        data() {
            return {
                username: '',
                password: '',
            }
        },
        validations: {
            username: {
                required,
                email,
            },
            password: {
                required,
                minLength: minLength(8),
            }
        },
        methods: {
            Login() {
                return new Promise((resolve, reject) => {
                    axios.post('https://reqres.in/api/login', {
                            username: this.username,
                            password: this.password
                        })
                        .then(response => {
                            if (response.data.token) {
                                localStorage.setItem("token", response.data.token);
                                this.$router.push("/users");
                            } else {
                                console.log("requir correct info")
                            }
                            resolve(response);
                        })
                        .catch(error => {
                            console.log(error);
                            reject(error);
                        })
                });
            }
        }
    }
</script>

<style lang="scss" scoped>
    .login-forms {
        padding: 10px 15px;
        background: #262626;
        position: absolute;
        left: 48%;
        top: 35%;
        border-radius: 9px;
        transform: translate(-50%, -50%);
        width: 400px;

        label {
            color: #7aa93c;
        }

        .form-group {
            margin-top: 15px;
        }

        .btn {
            background: #7aa93c;
            color: #fff;
            padding: 5px 15px;
        }

        .custom-link {
            margin-left: 10px;
            color: #7aa93c;
        }
        span{
            color:#2d98da
        }

    }
</style>