<template>
    <div class="resoures">
        <div class="container">
            <div class="text-center">
                <router-link to="/users" class="btn btn-dark">Back <i class="fas fa-backward"></i>
                </router-link>
            </div>
            <div class="row">
                <div class="col-md-4" v-for="resource in Resoures" :key="resource.id">
                        <div class="card" style="width: 18rem;">
                            <div class="card-header">
                                <span>Name</span> :  {{resource.name}}
                            </div> 
                             <div class="card-header">
                                <span>Year</span> :  {{resource.year}}
                            </div> 
                             <div class="card-header">
                                <span>color</span> :  {{resource.color}}
                            </div> 
                             <div class="card-header">
                                <span>pantone_value</span> :  {{resource.pantone_value}}
                            </div>
                             <router-link :to="'/singleResoure/' + resource.id" class="btn">View <i class="fas fa-backward"></i>
                              </router-link>
                          
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from "axios";
    export default {
        name: "resoures",
        data() {
            return {
                Resoures: {}
            }
        },
        methods: {
            getResoures() {
                axios.get("https://reqres.in/api/unknown").
                then(response => {
                    this.Resoures = response.data.data;
                    console.log(this.response);
                }).catch(error => {
                    console.log(error);
                })
            }
        },
        created() {
            this.getResoures();
        }
    }
</script>

<style lang="scss">
.resoures{
    margin-top: 50px;
    .card{
        margin-top: 20px;
        .card-header{
            cursor: pointer;
            transition: all .3s ease-in-out;
            &:hover{
                background: #262626;
                color: #fff;
            }
            span{
                 color: #7aa93c;
                 font-size: 18px;
                 font-weight: bold;
            }
        }
        .btn{
             background: #7aa93c;
             color: #fff;
             padding: 7px;
             font-size: 18px;
             font-weight: bold;
        }
    }
}
</style>