import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

export default new Vuex.Store({
  
  mutations: {
    setUsers() { 
       console.log("the users is added!")
    },
    user(){
      console.log("don")
    }
  },
  actions: {
    usersInfo({commit},id) {
      return new Promise((resolve,reject) =>{
        axios
        .get("https://reqres.in/api/users?page=" + id)
        .then((response) => {
          commit("setUsers");
          resolve(response);
        })
        .catch((error) => {
          console.log(error);
          reject(error);
        })
      });
    },
    getUser({commit},id) {
      return new Promise((resolve,reject) =>{
        axios
        .get(`https://reqres.in/api/users/${id}`)
        .then((response) => {
          commit("user");
          resolve(response);
        })
        .catch((error) => {
          console.log(error);
          reject(error);
        })
      });
    },
  },

});
