<template>
    <div class="header">
        <nav class="navbar navbar-expand-lg">
            <div class="container">
                <router-link to="/" class="navbar-brand mx-auto">Employees Management
                    <i class="fas fa-users note-icon"></i></router-link>
                    <button type="button" @click="logout()" class="btn">Logout</button>
            </div>
        </nav>
    </div>
</template>

<script>
    export default {
        name: 'navbar',
        data(){
            return{
                loggedIn :true
            }
        },
        methods:{
            logout(){
                this.loggedIn = true
                localStorage.removeItem("token")
                 this.$router.push({path: "/"})
 
            }
        }

    }
</script>

