<template>
    <div class="sing-up">
        <div class="alert alert-secondary" role="alert">
                <div> <span>Email:</span>eve.holt@reqres.in </div>
                <div><span>password</span>pistol</div>
            </div>
        <form>
            <div class="form-group">
                <label>Email</label>
                <input type="email" class="form-control" name="email" v-model.trim="$v.Email.$model"
                    :class="{'is-invalid':$v.Email.$error, 'is-valid':!$v.Email.$invalid }">
                <div class="valid-feedback">your Email is macted! </div>
                <div class="invalid-feedback">
                    <span v-if="!$v.Email.required">Your Email is requried</span>
                </div>
            </div>
            <div class="form-group">
                <label>password</label>
                <input type="password" id="password" class="form-control" name="password"
                    v-model.trim="$v.password.$model"
                    :class="{'is-invalid':$v.password.$error, 'is-valid':!$v.password.$invalid }">
                <div class="valid-feedback">your password is macted! </div>
                <div class="invalid-feedback">
                    <span v-if="!$v.password.required">Your password is requried</span>
                    <span v-if="!$v.password.minLength"> your password is have least
                        {{$v.password.$params.minLength.min}}</span>
                </div>
            </div>
            <div class="form-group form-check">
                <input type="checkbox" class="form-check-input" id="showpassword" @click="toggleShowpassword"
                    v-model="showPassword">
                <label class="form-check-label" for="showpassword">show the password</label>
            </div>
            <div class="form-group">
                <label>confirm password</label>
                <input type="password" class="form-control" name="password" v-model.trim="$v.confirmPassword.$model"
                    :class="{'is-invalid':$v.confirmPassword.$error, 'is-valid':(password !='')  ? //email vaule dont null 
                  !$v.confirmPassword.$invalid:'' }">
                <div class="valid-feedback">Your password is identical </div>
                <div class="invalid-feedback">
                    <span v-if="!$v.confirmPassword.sameAspassword">Your password must be identical</span>
                </div>
            </div>
        </form>
        <button type="submit" class="btn" @click="userRegister()">Sign Up</button>
    </div>
</template>


<script>
    import { required,minLength,email,sameAs} from 'vuelidate/lib/validators';
     import axios from "axios";
    export default {
        name: 'registration',
        data() {
            return {
                Email: '',
                password: '',
                confirmPassword: '',
                showPassword: false
            }
        },
        validations: {
            Email: {
                required,
                email
            },
            password: {
                required,
                minLength: minLength(6),
            },
            confirmPassword: {
                sameAspassword: sameAs('password')
            }
        },
        methods: {

            toggleShowpassword() {
                var show = document.getElementById('password')
                if (this.showPassword == false) {
                    this.showPassword = true;
                    show.type = 'text';
                } else {
                    this.showPassword = false;
                    show.type = 'password';
                }
            },
            userRegister() {
                return new Promise((resolve,reject)=> {
                       axios.post('https://reqres.in/api/register', {
                        Email: this.Email,
                        password: this.password
                    })
                    .then(response => {
                        if(response.data.token){
                        localStorage.setItem("token",response.data.token);
                        this.$router.push("/users");
                       resolve(response);
                        }else{
                           console.log("requir correct info")
                        }
                    })
                    .catch(error => {
                        console.log(error)
                        reject(error);
                    })
            });
           }      
        }
    }
</script>

<style lang="scss" scoped>
    .sing-up {
        padding: 10px 15px;
        background: #262626;
        position: absolute;
        left: 50%;
        top: 45%;
        border-radius: 9px;
        transform: translate(-50%, -50%);
        width: 500px;

        label {
            color: #7aa93c;
        }

        .form-group {
            margin-top: 15px;
        }

        .btn {
            background: #7aa93c;
            color: #fff;
            padding: 5px 15px;
            width: 100%;
            text-align: center;
        }
         
        span{
            color:#2d98da
        }
    }


    @media (max-width: 575.98px) {
        .sing-up {
            position: absolute;
            top: 55%;
            padding: 5px 8px;
            width: 100%;
        }
    }
</style>