<template>
<div class="container">
<div class="single">
        <div class="row">
            <div class="col-md-7">
                  <div class="card" style="width: 18rem;">
                            <div class="card-header">
                             <span>ID</span> : {{Resource.id}}
                            </div> 
                             <div class="card-header">
                                <span>Name</span> :  {{Resource.name}}
                            </div> 
                             <div class="card-header">
                                <span>year</span> :  {{Resource.year}}
                            </div> 
                            <div class="card-header">
                                <span>color</span> : <b>{{Resource.color}} </b> 
                            </div> 
                             <div class="card-header">
                                <span>pantone_value</span> : {{Resource.pantone_value}}
                            </div>
                             <div class="card-header">
                                <span>copmany</span> : {{ourCompany.company}}
                            </div>
                          
                    </div>
                          <router-link to="/users" class="btn btn-dark">Back <i class="fas fa-backward"></i>
                          </router-link>
                
            </div>
        </div>
    </div>
</div>
</template>

<script>
import axios from "axios";
export default {
      mounted() {
      this.getSingleResoure();
  },
    data(){
        return{
            param:this.$route.params.id,
            Resource:[],
            ourCompany:{}
        }
    },
    methods:{
        getSingleResoure(){
                 axios.get("https://reqres.in/api/unknown/" + this.param).
            then(respones => {
                this.Resource = respones.data.data;
                this.ourCompany = respones.data.ad;
            }).catch(error =>{
                console.log(error);
            })
             
        }
    },
}
</script>

<style lang="scss">
    .single{
        position: absolute;
        top: 45%;
        left: 50%;
        transform: translate(-50%, -50%);
          span{
                 color: #7aa93c;
                 font-size: 18px;
                 font-weight: bold;
            }
            .btn{
                margin-top: 30px;
            }
    }
</style>