<template>
    <div class="container">
        <div class="singleUser">
            <div class="card" style="width: 18rem;">
                <div class="img-user text-center">
                    <img :src='User.avatar'>
                </div>
                <div class="card-header text-center">
                    <h1>{{User.first_name}} {{User.last_name}}</h1>
                </div>
                <div class="card-header">
                    <span>Id</span> : <span class="userInfo">{{User.id}}</span>
                </div>
                <div class="card-header">
                   <span>Email</span> : <span class="userInfo">{{User.email}}</span>
                </div>
                <div class="card-header">
                 <span>Company</span> : <span class="userInfo">{{ourCompany.company}}</span>
                </div>
                <div class="socialMedia text-center">
                    <i class="fab fa-facebook-square icon"></i>
                    <i class="fab fa-instagram-square icon"></i>
                    <i class="fab fa-twitter icon"></i>
                    <i class="fab fa-github icon"></i>
                </div>
                <router-link to="/users" class="btn btn-dark">Back <i class="fas fa-backward"></i>
                </router-link>
            </div>
        </div>
    </div>
</template>

<script>
import axios from "axios";
    export default {
        mounted(){
            this.usersInfo();
        },
        data() {
            return {
                param: this.$route.params.id,
                User: [],
                ourCompany:[]
            }
        },
        methods: {
        usersInfo() {
          return new Promise((resolve,reject) =>{
        axios
        .get("https://reqres.in/api/users/" + this.param)
        .then((response) => {
           this.User =  response.data.data;
           this.ourCompany = response.data.ad;
          resolve(response);
        })
        .catch((error) => {
          console.log(error);
          reject(error);
        })
      });
        }
        }
    }
</script>

<style lang="scss">
    .singleUser {
        position: absolute;
        top: 45%;
        left: 50%;
        transform: translate(-50%, -50%);

        .card {
            border: 1px solid #7aa93c;
            .img-user {
                padding: 20px 10px;
                img{
                    width: 70px;
                    height: 70px;
                    border-radius: 50%;
                     border: 1px solid #7aa93c;
                }
            }
            .socialMedia {
                padding: 10px 15px;

                .icon {
                    font-size: 25px;
                    margin-left: 15px;
                    cursor: pointer;
                    color: #7aa93c;
                }
            }
              span{
                 color: #7aa93c;
                 font-size: 18px;
                 font-weight: bold;
            }
            .userInfo{
                color: #000;
                font-weight: bold;
            }
        }
    }
</style>