<template>
    <div class="container">
        <div class="adduser">
            <div class="updata">Updata Employee<i class="fas fa-user"></i></div>
            <div class="text-center user-info">
                 {{user.first_name}} {{user.last_name}}
                 <hr>
            </div>
            <form action="#">
                <div class="form-group">
                    <label>Name</label>
                    <input type="text" class="form-control" v-model="name">
                </div>
                <div class="form-group">
                    <label>Job</label>
                    <input type="text" class="form-control" v-model="job">
                </div>
                <button type="submit" class="btn add" @click="apdataUser()">updata <i class="fas fa-pen"></i></button>
                <router-link to="/users" class="btn btn-dark">Back <i class="fas fa-backward"></i></router-link>
            </form>
        </div>
    </div>
</template>
<script>
    import axios from "axios";
    export default {
           mounted(){
             this.getUser();
         },
        data() {
            return {
                param: this.$route.params.id,
                user:[],
                name: "",
                job: "",        
            }
        },
        methods: {
               getUser(){
                this.$store.dispatch("getUser" , this.param).
                then(response =>{
                    this.user = response.data.data;
                }).catch(error =>{
                    console.log(error);
                })
            },
            apdataUser() {
                axios.put('https://reqres.in/api/users' + this.param, {
                    name: this.name,
                    job: this.job,
                }).then((respones) => {
                    console.log(respones.data);
                }).catch(error => {
                    console.log(error);
                })
            },
        },
    }
</script>

<style lang="scss" scoped>
    .adduser {
        margin-top: 50px;
        width: 400px;
        padding: 8px 10px;
        border-radius: 8px;
        background: #dcdde1;
        .user-info{
            padding: 15px;
            font-size: 20px;
            font-weight: bold;
            color: #7aa93c;
            hr{
                width: 50px;
                height: 2px;
                background:#7aa93c ;
            }
        }
        .updata {
            text-align: center;
            padding: 6px;
            font-size: 18px;
            color: #fff;
            font-weight: bold;
            background: #3498db;
        }

        form {
            margin-top: 15px;
        }

        label {
            color: #7aa93c;
            font-weight: bold;
        }

        .add,
        .btn-dark {
            color: #fff;
            font-weight: bold;
            padding: 6px 20px;
            margin-right: 8px;
        }

        .add {
            background: #3498db;
        }
    }
</style>