<template>
    <div class="usersTable">
        <div class="container">
            <div class="adduser text-center">
                <nav aria-label="Page navigation example">
                    <ul class="pagination">
                        <li class="page-item" :class="page === 1 ? 'disabled' : ''">
                            <a class="page-link" @click="prevpage()">Previous
                        </a></li>
                        <li class="page-item">
                            <a class="page-link" href="#">{{page}} of {{Allpages}}</a>
                            </li>
                        <li class="page-item" :class="page === Allpages ? 'disabled' : ''">
                            <a class="page-link" @click="nextpage()">Next</a>
                            </li>
                    </ul>
                </nav>
                <router-link to="/addUser" class="btn btn-primary"> Create <i class="fas fa-plus"></i></router-link>
                <router-link to="/resoures" class="btn btn-dark"> Resoures</router-link>
            </div>
            <div class="row">
                <div class="col-md-12">
                    <table class="table">
                        <thead class="head">
                            <tr>
                                <th scope="col">ID</th>
                                <th scope="col">FirstName</th>
                                <th scope="col">LastName</th>
                                <th scope="col">email</th>
                                <th scope="col">avatar</th>
                                <th scope="col">options</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr class="col" v-for="(user,i) in users" :key="i">
                                <th scope="row">{{user.id}}</th>
                                <td>{{user.first_name}}</td>
                                <td>{{user.last_name}}</td>
                                <td>{{user.email}}</td>
                                <td><img :src='user.avatar'></td>
                                <td>
                                    <router-link :to="'/edit/' + user.id" class="btn btn-primary">Updata <i
                                            class="fas fa-check"></i></router-link>
                                    <button type="button" class="btn btn-danger" @click="userDelete(user.id)">Delete<i
                                            class="fas fa-trash"></i></button>
                                    <router-link :to="'/singleUser/'+ user.id" class="btn btn-primary">View <i
                                            class="fas fa-eye"></i>
                                    </router-link>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import axios from "axios";
    export default {
         mounted(){
             this.getUsers();
         },
        data() {
            return {
                users:[],
                page:1,
                Allpages:1
            }
        },
        methods: {
            getUsers(){
                this.$store.dispatch("usersInfo" , this.page).
                then(response =>{
                    this.users = response.data.data;
                    this.Allpages = response.data.total_pages;
            
                }).catch(error =>{
                    console.log(error);
                })
            },
            userDelete(id, i) {
                axios
                    .delete(`https://reqres.in/api/users/${id}`)
                    .then(() => {
                        this.users.splice(i, 1);
                    })
                    .catch((error) => {
                        console.log(error);
                    });
            },
            prevpage(){
                this.page--;
                this.getUsers()
            },
            nextpage(){
                this.page++;
                this.getUsers();
            }
        },
    }
</script>

<style lang="scss" scoped>
    .usersTable {
        .adduser {
            margin-bottom: 10px;
        }

        .head {
            background: rgba(38, 38, 38, .5);
            color: #fff;
            font-size: 18px;
        }

        padding-top: 40px;

        .table {
            border-radius: 8px;
            background: #dcdde1 !important;
            text-align: center;
        }

        .btn {
            margin-left: 3px;
        }

        .col {
            transition: all .1s ease-in-out;
            cursor: pointer;
            /*
           &:hover{
               background: rgba(38, 38, 38, .5);
               color: #fff;
               font-weight: bold;
           }
           */
        }
    }
</style>