<template>
    <div class="container">
        <div class="adduser">
            <div class="add-icon">Add New Employee <i class="fas fa-user"></i> </div>
            <form action="#" >
                <div class="form-group">
                    <label>Name</label>
                    <input type="text" class="form-control" v-model="name">
                </div>
                <div class="form-group">
                    <label>Job</label>
                    <input type="text" class="form-control" v-model="job">
                </div>
                <button type="submit" class="btn add" @click="addUser()">ADD<i class="fas fa-check"></i></button>
                <router-link to="/users" class="btn btn-dark">Back <i class="fas fa-backward"></i></router-link>
            </form>
        </div>
    </div>
</template>
<script>
      import axios from "axios";
    export default {
        name: 'User',
        data() {
            return {    
                    name:"",
                    job:"",
            
                 
            }
        },
        methods: {
          addUser(){
                axios.post('https://reqres.in/api/users',{
                  name:this.name,
                  job:this.job,
              }).then(response =>{
                  console.log(response.data)
              }).catch(error =>{
                  console.log(error);
             
              })
            }
        } 
    }
</script>

<style lang="scss" scoped>
    .adduser {
        margin-top: 50px;
        width: 400px;
        padding: 8px 10px;
        border-radius: 8px;
        background: #dcdde1;

        .add-icon {
            text-align: center;
            padding: 6px;
            font-size: 18px;
            color: #fff;
            font-weight: bold;
            background: #7aa93c;
        }

        form {
            margin-top: 15px;
        }

        label {
            color: #7aa93c;
            font-weight: bold;
        }

        .add,
        .btn-dark {
            color: #fff;
            font-weight: bold;
            padding: 6px 20px;
            margin-right: 8px;
        }

        .add {
            background: #7aa93c;
        }
    }
</style>